



















































@import '@design';
.sider {
  :global(.ant-layout-sider-children) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .menu {
    flex: 1;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .avatar {
    margin-bottom: 30px;
  }
  .bottom {
    line-height: 48px;
    font-size: 1rem;
    padding: 25px 0;
  }
}

.header {
  background-color: $color-primary;
  padding: 0;
  color: white;
  .title {
    text-transform: uppercase;
    float: right;
    padding-right: 15px;
    letter-spacing: 0.1em;
  }
}
.toggler {
  padding: 0 15px;
}
